  flex-shrink: 0;
  height: var(--hamburger-layer-height);
  position: relative;
  transition: all 0.2s ease 0s;
  width: var(--hamburger-width);
  border-radius: var(--hamburger-layer-height);
}
.hamburger::before, .hamburger::after {
  background-color: var(--hamburger-color);
  content: "";
  display: block;
  height: var(--hamburger-layer-height);
  position: absolute;
  transition: all 0.2s ease 0s;
  width: var(--hamburger-width);
  border-radius: var(--hamburger-layer-height);
}
.hamburger::before {
  top: -0.5625rem;
}
.hamburger::after {
  bottom: -0.5625rem;
}

